import empty from "@/components/empty";
import Json from '@/Json';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [],
			orderList:[],
			
			source: 0,
			addressList: [],
			scrollHeight: 100,
			tabScrollTop: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
		};
	},
	
	onLoad(options){
		this.tabCurrentIndex = options.state
		console.log('sdfd',this.tabCurrentIndex);
		this.calcSize();
	},
	onShow(){
		this.page = 1
		this.limit = 20
		this.orderList = []
		this.getUserOrderList();
	},
	 
	methods: {
		getUserOrderList(){
			let self = this;
			let path = '/gzh/shop_api/order_list'
			let data = {
				page:self.page,
				limit:self.limit
			}
			getApp().http(path, data, function(res) {
				
				let list = res;
				if (list.length > 0) {
					self.status = list.length <= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
						console.log(self.page);
						console.log('=================444444444444')
					}
					self.orderList = self.orderList.concat(list);
					console.log('都发给对方',self.orderList)
				} else {
					self.status = "";
				}
				
				console.log('水电费水电费',self.orderList,list);
			})
		},
		//顶部tab点击
		tabClick(index,item){
			let self = this;
			self.tabCurrentIndex = index;
			let type = item.state
			
			self.orderList = [];
			self.page = 1;
			self.getUserOrderList(type);
			console.log('点击',this.tabCurrentIndex,type)
			
		},
	
	
		indexUrl(icon) {
			let http = icon.indexOf('http')
			if (http == -1) {
				return this.webUrl + '/' + icon
			} else {
				return icon;
			}
		},
		
		// 到底触发
		scroAddr(){
			let self = this;
			if(self.status){
				self.getUserOrderList();
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			var self = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					let info = uni.createSelectorQuery().select(".content");
					self.scrollHeight = res.windowHeight 
					// self.scrollHeight = 300
					console.log(info);
					console.log('啊啊啊啊',res);
				}
			});
		},
		
		orderdet(item){
			uni.navigateTo({
				url: "/pages/integral/intOrderDetail?id="+item.id
			})
		},
		
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		

	},
}
